import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			token:'',
			memberInfo:[]
		};
	},
	onShow() {
		if(uni.getStorageSync('cbe_token')){
			this.token = uni.getStorageSync('cbe_token');
			this.getMemberInfo();
		}else{
			uni.showToast({ title:"未登录或无权限",icon: "none"});
			var timer = setTimeout(()=>{
				uni.navigateBack({
					delta:1
				})
			},1000);
		}
	},
	methods:{
		...mapMutations(['logout']),
		lianxi(){
			uni.showModal({
				title:"客服热线",
			    content: '400-004-5678',
				confirmColor:'#008AB0',
				confirmText:'现在拨打',
			    success: (e)=>{
			    	if(e.confirm){
						uni.makePhoneCall({
							phoneNumber: '400-004-5678'
						})
			    	}
			    }
			});
		},
		getMemberInfo(){
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/member/info',
				data: {token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					var one = resa.data;
					if(one.code==0){
						one.one['phone'] = one.one.mobile.substr(0, 3) + '****' + one.one.mobile.substr(7);
						this.memberInfo = one.one;
					}
				}
			})
		},
		navTo(url){
			uni.navigateTo({
				url:url
			})
		},
		navToa(url,keywords,val){
			if(uni.getStorageSync('cbe_anquan')){
				uni.navigateTo({
					url:url+"?keywords="+keywords+'&val='+val
				})
			}else{
				uni.navigateTo({
					url:"/pages/set/anquan?keywords="+keywords+'&val='+val
				})
			}
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
				confirmColor:'#008AB0',
			    success: (e)=>{
			    	if(e.confirm){
						uni.removeStorageSync('cbe_token');
			    		this.logout();
			    		setTimeout(()=>{
			    			uni.navigateBack();
			    		}, 200)
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
